// Import Libraries & Files //
import React from 'react';
// ----- //
import './Support_us.css';
// ------------------------ //

// Funtion showhide support_us description

function follow_us() {
  var x = document.getElementById("follow_us");
  x.style.display = "block";
}

function follow_us_none() {
  var x = document.getElementById("follow_us");
  x.style.display = "none";
}

function member() {
  var x = document.getElementById("member");
  x.style.display = "block";
}

function member_none() {
  var x = document.getElementById("member");
  x.style.display = "none";
}

// Support Us

function Support_us() {
  return (
  <section id="Support_us">
    <div className="container-fluid text-center h-100">
      <div className="row h-100 mx-auto">

        {/* section header */}
        <div className="col-xl-12 my-auto">
          <header>
            <h1> Support Us </h1>
            <br />
          </header>
        </div>

        {/* section content */}

        <div className="col-xl-2 col-sm-1 col-12 my-auto">
        </div>

        {/* follow us */}
        <div className="col-xl-4 col-sm-5 col-12">
          <div onMouseOver = {follow_us} onMouseLeave = {follow_us_none} className="container support_us-out d-flex justify-content-center flex-column">
            <div id="follow_us" className="support_desc">
              <h1> Follow Us </h1>
              <p> You can help us for free, share our posts and keep in the loop with us on social media
              </p>
              <br />
              <a href="#footer">
                <button id="red_btn" type='button' className='btn btn-lg'>
                  <b> Follow us </b>
                </button>
              </a>
            </div>
            <h1> Follow Us </h1>
            <img className="img-fluid mx-auto my-auto" src="%PUBLIC_URL%/img/web_img/follow-us-img.png" />
          </div>
        </div>

        {/* Become a member */}
        <div className="col-xl-4 col-sm-5 col-12 ">
          <div onMouseOver = {member} onMouseLeave = {member_none} className="container support_us-out d-flex justify-content-center flex-column">
          <div id="member" className="support_desc">
            <h1> Become a member </h1>
            <p>  Its why this society exists. Support us direclty by become our society member
                 and see us grow...
            </p>
            <br />
            <a href="https://www.napierstudents.com/organisation/societies/napierenterprise/">
              <button id="blue_btn" type='button' className='btn btn-lg'>
                <b> Join us </b>
              </button>
            </a>
          </div>
            <h1> Become a member </h1>
            <img className="img-fluid mx-auto my-auto" src="%PUBLIC_URL%/img/web_img/become-member-img.png" />
          </div>
        </div>

        <div className="col-xl-2 col-sm-1 col-12 my-auto">
        </div>

      </div>
    </div>
  </section>
  );
  }

  // Exporting function 'App', to wherever its being called to
  export default Support_us;
